import React from 'react';
//import logo from './logo.svg';

import './App.css';
//import './api/request.js';
import AppBar from './components/AppBar';
import BottomAppBar from './components/BottomAppBar';
import Cart from './components/Cart';
import Products from './components/Products';
import Home from './components/Home';
import SignUp from './components/SignUp';
import SignIn from './components/SignIn';

import Dashboard from './components/Dashboard';
import Wishlist from './components/Wishlist';
import Notification from './components/Notification';
import NotFound from './components/notFound';
import UserInfo from './components/UserInfo';
import SecurityPrivacy from './components/Security&Privacy';
import Test from './components/Test';
import * as Colors from '@mui/material/colors';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Routes as Switch, Route} from 'react-router-dom';
import {useCustomDispatch,useCustomSelector} from './states/hook';
import {getCookie} from './assets/js/cookies.js'
import {increment,getFromCart,addProducts,CartItemType} from './states/cartItems';
import axios from 'axios';
declare module '@mui/material/styles' {
  interface Theme {
    status: {
      danger: string;
    };
    text: {
      primary: string;
    }
  }
  // allow configuration using `createTheme`
  interface ThemeOptions {
    status?: {
      danger?: string;
    };
    text?: {
      primary: string;
    }
  }
}



const theme = createTheme({
  palette: {
    mode: 'light',
    primary:{
      main:Colors.common.white,
    },
    text: {
      primary:Colors.blue[300],
      secondary: Colors.common.white,
    },
    secondary: {
      main:Colors.yellow[800],
    }
    
  }
});




function App() {
  const dispatch = useCustomDispatch();
   const token = useCustomSelector((state) => state.user.value);
   const myCart = useCustomSelector((state) => state.cart.myProducts);
   React.useEffect(()=>{
         getFromCart()
    .then((response:any)=>{
      //alert('here'+JSON.stringify(response));
      if(response.status){
        if(response.cart.length > 0){
          response.cart.forEach((product:CartItemType, index:number)=>{
            dispatch(increment({productId:product.productId,product:product,count:product.count, amount:response.amount}));
            //alert(response.data.amount)
          });
        }
        
      }
      if(response.status) {
          let products: any[]= [];
        //@ts-ignore
        for(let i=0; i < response.products.length;i++) {
          response.products[i].count = 1;
          products.push(response.products[i]);
        } 
        dispatch(addProducts(products));
      } else {
        // Executed when the cart list is empty;
        //FALSE EXECUTED;
        if(response.products.length > 0){
          //alert(response.products.length);
          response.products.forEach((product:CartItemType,index:number)=>{
            dispatch(increment({productId:product.productId,product:product,count:index + 1, amount:response.amount}));
          });
          dispatch(addProducts(response.products));
          
        }
      }
    })
    .catch((err:Error)=>{
      //Executed when a network error occurs
      //setMessage(err.message);
      
    });
    
   })
  return (
    <div className="App">
   
    <ThemeProvider theme={theme}>
    <AppBar />
    
      <Switch>
        <Route  path='/' element={<Home />} />
        <Route path='/product' element={<Products />} />
        <Route path='/cart' element={<Cart />} />
        <Route path='/sign-up' element={<SignUp/>} />
        <Route path='/sign-in' element={<SignIn />} />
        <Route path='/wishlist' element={<Wishlist />} />
        <Route path='/dashboard' element={<Dashboard xs={12} sm={6}/>} />
        <Route path='/user-info' element={<UserInfo />} />
        <Route path='/security-privacy' element={<SecurityPrivacy />} />
        <Route path='/notifications' element={<Notification />} />
        
        <Route path='*' element={<NotFound/>} />
        
      </Switch>
      <BottomAppBar />
      </ThemeProvider>
    </div>
  );
}

export default App;
