import { createSlice } from '@reduxjs/toolkit';
import axios from 'axios';
import {getCookie} from '../assets/js/cookies.js'
export interface CartItemType {
  count: number;
  amount: number;
  productId: string;
}
interface CartItemsType {
  count: number;
  products: CartItemType[];
}
interface Test {
  count: number;
  productId: string;
}
const CartItems: CartItemsType = {
  count: 0,
  products: []
}
const items: CartItemType[] = [];
const cart : Test[] = [];
export async function getFromCart(){
  //@ts-ignore
  let user = JSON.parse(getCookie('belaiexpress'));
  if(user != null) {
    return new Promise((resolve,reject)=>{
      //let window.location.search.split('')[1];
      axios.get('/cart',{params:{userId:user.userId}})
    .then((response:any)=>{
      //alert(JSON.stringify(response));
       resolve(response.data);
    })
    .catch((err:Error)=>{
      //alert(JSON.stringify(err));
      reject(err);
    })
    });
  } else {
    return new Promise((resolve,reject)=>{
      
    if(localStorage['belaiexpress']) {
         let products_local = JSON.parse(localStorage['belaiexpress']);
         let temp_products = [];
         for(let i=0; i<products_local.length;i++){
           temp_products.push(products_local[i]['productId']);
         }
        axios.get('/products/specific',{params:{products: temp_products}})
         .then((data:any)=>{
           let response = data.data;
           if(response.status) {
              resolve({status:false, cart:JSON.parse(localStorage['belaiexpress']),products:response.products});
           }
         })
         .catch((err: Error)=>{
            resolve({status:false, cart:JSON.parse(localStorage['belaiexpress']),products:[]});
         });
         
     
    } else {
      
      reject({status:false, message:'nothing in the cart',cart:cart,products:items});
    }
  });
  }
}
  //@ts-ignore
const userGlobal = JSON.parse(getCookie('belaiexpress'));
function addToCart(product_id:string,count:number){
  //@ts-ignore
  let user = JSON.parse(getCookie('belaiexpress'));
  return new Promise((resolve,reject) =>{
  if(user != null){
     axios.post('/cart',{userId:user.userId,count:count,productId:product_id})
          .then((response:any)=>{
            if(response.data.status){
              resolve(response.data);
            } else {
              reject(response.data);
            }
          })
          .catch((err: Error)=>{
            reject(err);
          });
  } else {
    if(localStorage['belaiexpress']){
      let myCart = JSON.parse(localStorage['belaiexpress']);
      let check = myCart.filter((product:Test) => product.productId === product_id);
      if(check.length > 0) {
        //Executed when the product already exists
        //localStorage.remove('belaiexpress')
      } else {
         myCart.push({count:count,productId:product_id});
         localStorage['belaiexpress'] = JSON.stringify(myCart);
      }
     
      resolve({cart:JSON.parse(localStorage['belaiexpress']),products:items});
    } else {
        let myCart = [{count:count,productId:product_id}];
        localStorage['belaiexpress'] = JSON.stringify(myCart);
         resolve({notLogged:true,cart:JSON.parse(localStorage['belaiexpress']),products:items});
    }
  }
});
}
export async function initCart(){
  let data = await getFromCart();
  return data;
}
